import React from 'react'
import ReactPlayer from 'react-player'
import { Link } from 'react-router-dom';
import {
  CBadge,
  CRow,CCol
} from '@coreui/react'
import  {Mydate, requestOptions,videoUrl} from '../Config'

class Playback extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        videoList: []
      };
    }
    componentDidMount() {
          fetch(videoUrl, requestOptions)
           .then((response) => response.json())
           .then((data) => {  this.setState({videoList:data})  })
           .catch(error => console.log("Error detected: " + error))
     }

  render() {
  return <>
    <a href="http://google.com" className="text-body h5">  {'<'} Back</a>

    <div className='player-wrapper'>
                  <ReactPlayer
                    className='react-player'
                    url={[{src: this.props.playback.video, type: 'video/mp4'} ]}
                    width='100%'
                    height='100%'
                    controls
                  />
                  <p className="h3"><strong>{this.props.playback.title}</strong></p>
                  <p className="h6">Publish by: {this.props.playback.teacher}</p>
                  <div className="small text-muted">
                      {Mydate(this.props.playback.startDateTime)}
                  </div>
              
                </div>
    
    <br/><br/>
      <span className="h5"><strong>More Videos</strong></span>
    <br/><br/>
    <CRow>
        { this.state.videoList.map((video,index)=>( 
          <CCol sm="6" lg="3" key={index}>
                <Link to="/playback" onClick={()=>this.props.getVideoHandler(video)}>
                  <div className='player-wrapper'>
                        <ReactPlayer
                          className='react-player'
                          url={[{src: `${this.state.video}`, type: 'video/mp4'} ]}
                          width='100%'
                          height='100%'
                          controls
                        />
                        {}
                        <CBadge color="success" >New</CBadge>
                        <div><strong>{video.title}</strong></div>
                        <div className="small text-muted">
                            <span>New</span> | {video.batch}: {Mydate(video.startDateTime)}
                        </div>
                    
                      </div>
                </Link>     
                </CCol>  
        ))}
    </CRow>
     </>
  }
}

export default Playback
